.card-top .top-left-corner {
  position: absolute;
  top: 5px;
  left: 5px;
}

.top-left-corner .add-recipe-to-list .button-lists {
  top: calc(100% + 5px);
  left: 0;
  width: 210px;
}

.top-left-corner .add-recipe-to-list > button.icon-button {
  border-radius: 50%;
  padding: 0px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  .icon {
    padding: 0;
  }
}

.card-bottom h4 {
  margin-bottom: 10px;
}

.card-bottom .cat-list {
  padding: 0;
  margin: 0;
  list-style: none;
  > li {
    display: inline-block;
    margin-left: 0;
    margin-bottom: 6px;
    a {
      background: var(--input-bg);
      color: hsl(216, 15%, 42%);
      padding: 0 5px;
      border-radius: 3px;
      font-size: 12px;
      font-weight: 600;
      letter-spacing: 0.5px;
      &:hover {
        color: var(--link-color);
      }
    }
  }
}
